import axios from '@nextcloud/axios';
import NcSelect from '@nextcloud/vue/dist/Components/NcSelect.js';
import NcButton from '@nextcloud/vue/dist/Components/NcButton.js';
import NcCheckboxRadioSwitch from '@nextcloud/vue/dist/Components/NcCheckboxRadioSwitch.js';
import NcSettingsSection from '@nextcloud/vue/dist/Components/NcSettingsSection.js';
import { loadState } from '@nextcloud/initial-state';
import sortedUniq from 'lodash/sortedUniq.js';
import uniq from 'lodash/uniq.js';
import debounce from 'lodash/debounce.js';
import { generateUrl, generateOcsUrl } from '@nextcloud/router';
export default {
  name: 'AdminTwoFactor',
  components: {
    NcSelect,
    NcButton,
    NcCheckboxRadioSwitch,
    NcSettingsSection
  },
  data() {
    return {
      loading: false,
      dirty: false,
      groups: [],
      loadingGroups: false,
      twoFactorAdminDoc: loadState('settings', 'two-factor-admin-doc')
    };
  },
  computed: {
    enforced: {
      get() {
        return this.$store.state.enforced;
      },
      set(val) {
        this.dirty = true;
        this.$store.commit('setEnforced', val);
      }
    },
    enforcedGroups: {
      get() {
        return this.$store.state.enforcedGroups;
      },
      set(val) {
        this.dirty = true;
        this.$store.commit('setEnforcedGroups', val);
      }
    },
    excludedGroups: {
      get() {
        return this.$store.state.excludedGroups;
      },
      set(val) {
        this.dirty = true;
        this.$store.commit('setExcludedGroups', val);
      }
    }
  },
  mounted() {
    // Groups are loaded dynamically, but the assigned ones *should*
    // be valid groups, so let's add them as initial state
    this.groups = sortedUniq(uniq(this.enforcedGroups.concat(this.excludedGroups)));

    // Populate the groups with a first set so the dropdown is not empty
    // when opening the page the first time
    this.searchGroup('');
  },
  methods: {
    searchGroup: debounce(function (query) {
      this.loadingGroups = true;
      axios.get(generateOcsUrl('cloud/groups?offset=0&search={query}&limit=20', {
        query
      })).then(res => res.data.ocs).then(ocs => ocs.data.groups).then(groups => {
        this.groups = sortedUniq(uniq(this.groups.concat(groups)));
      }).catch(err => console.error('could not search groups', err)).then(() => {
        this.loadingGroups = false;
      });
    }, 500),
    saveChanges() {
      this.loading = true;
      const data = {
        enforced: this.enforced,
        enforcedGroups: this.enforcedGroups,
        excludedGroups: this.excludedGroups
      };
      axios.put(generateUrl('/settings/api/admin/twofactorauth'), data).then(resp => resp.data).then(state => {
        this.state = state;
        this.dirty = false;
      }).catch(err => {
        console.error('could not save changes', err);
      }).then(() => {
        this.loading = false;
      });
    }
  }
};